import * as pointer from 'json-pointer';
import * as _ from 'lodash';

function Provider() {
    let config = {
        fxAction: null,
        sfSelect: null
    };

    function reduceHelper(obj, i) {
        return obj[i]
    }

    class Builder {
        static $inject = ["form", "fxAction"];

        searchText: string;
        selected: any;
        formData: any;

        constructor(private form, modelValue, formData) {
            this.init(modelValue, formData);
        }

        init(modelValue, formData) {
            this.formData = formData;
            if (!_.isEmpty(modelValue)) {
                this.onChange(modelValue);
            }
        }

        /**
         * 当数据变化时，触发事件
         * @param item 更改后的item
         */
        onChange(item) {
            if (_.isEmpty(item)) {
                return pointer.remove(this.formData, `/${this.form.key.join('/')}`);
            }
            this.selected = item;
            if (this.form.acOptions.keyField) {
                if (pointer.has(item, `/${this.form.acOptions.keyField}`)) {
                    return pointer.set(this.formData, `/${this.form.key.join('/')}`, pointer.get(item, `/${this.form.acOptions.keyField}`));
                } else {
                    console.error(`autocomplete-1-没有在item中找到${this.form.acOptions.keyField}`);
                    return pointer.remove(this.formData, `/${this.form.key.join('/')}`);
                }
            }

            config.sfSelect(this.form.key, item, this.formData);


            pointer.set(this.formData, `/${this.form.key.join('/')}`, item);
        }

        /**
         * 查询数据，如果存在actionKey，则获取actionKey中的interface数据
         * 查询接口，返回数据
         * @returns {any}
         */
        query() {
            let actionModel, clientData = {};
            let filter = {};

            if (this.form.acOptions.actionKey) {
                // 设置搜索条件
                pointer.set(filter, this.form.acOptions.search, this.searchText);

                // 设置全局条件
                _.forEach(this.form.acOptions._where, (val, key)=> {
                    pointer.set(filter, key, val);
                });

                return config.fxAction.getModel(this.form.acOptions.actionKey).then((aModel)=> {
                    actionModel = aModel;
                    return config.fxAction.doAction(actionModel.key, filter);
                }).then((results)=> {
                    return config.fxAction.doDealResult(actionModel, results, clientData);
                }).then((results)=> {
                    return results[this.form.acOptions.dataField];
                });
            }

            return this.form.data || [];
        }
    }

    /**
     * autocomplete builder函数
     * 初始化参数
     * 初始化搜索功能
     * @param args
     */
    this.builder = function (args) {
        args.form.acOptions = _.extend({
            textField: "",
            keyField: "",
            dataField: "",
            noCache: false,
            _where: {},
            search: "",
            actionKey: ""
        }, args.form.acOptions || {});
        args.form.ACBoost = (form, modelValue, formData)=> {
            console.log(args);
            return new Builder(form, modelValue, formData);
        };
    };

    this.$get = ["fxAction", "sfSelect", function (fxAction, sfSelect) {
        config.fxAction = fxAction;
        config.sfSelect = sfSelect;

        return config;
    }];
}

export default (module: ng.IModule)=> {
    module.provider('autoCompleteBuilder', [Provider]);
};


